/* Button design inspired and amended from: https://codepen.io/dhanishgajjar/pen/NgQqVj */

/* Base button styles */
.button {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  font-weight: bold;
  color: white;
  border-radius: 50px;
  text-decoration: none;
  overflow: hidden;
  background: #4d3f78; /* Tailwind Blue-600 */
  transition: background-color 0.3s, transform 0.3s;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1), 20px 20px 40px rgb(77, 63, 120, 0.1), 8px 8px 60px rgb(77, 63, 120, 0.3), inset 4px 4px 8px rgb(77, 63, 120, 0.2), inset 4px 4px 16px rgba(255, 255, 255, 0.3), inset -4px -4px 12px rgb(0, 0, 0, 0.2);
}

/* Animated gradient background */
.gradient {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background: radial-gradient(90px circle at top center, rgba(180, 83, 9, 0.8) 30%, rgba(255, 255, 255, 0));
  transform: translatex(-140px);
  animation: move 6s linear infinite;
}

/* Button hover effects */
.button:hover {
  background: #2e1065; /* Slightly darker Tailwind Blue */
  transform: scale(1.05);
}

.button:hover .gradient {
  transform: translatex(140px);
  transition: transform 0.1s ease-out;
}

/* Keyframes for the gradient animation */
@keyframes move {
  0% {
    transform: translatex(-140px);
  }
  25% {
    transform: translatex(140px);
    opacity: 0.3;
  }
  50% {
    transform: translatex(140px);
    opacity: 1;
    background: radial-gradient(90px circle at bottom center, rgba(180, 83, 9, 0.8) 30%, rgba(255, 255, 255, 0));
  }
  75% {
    transform: translatex(-140px);
    opacity: 0.3;
  }
  100% {
    opacity: 1;
    transform: translatex(-140px);
    background: radial-gradient(90px circle at top center, rgba(238, 88, 63, 0.5) 30%, rgba(202, 46, 46, 0));
  }
}
